/* BLOCKS */

.block {

	margin: {
		top: $space/2;
		bottom: $space/2;
	}
	@include insideMargin(0.5em);
	padding: $space/4 $space/2;

	&-title {

	}
	&-link {

	}
	&-icon {
		height: 1.35rem;
		width: 1.35rem;
		margin-right: 0.5rem;
	}

	// CRS Container
	.crs-wrapper & {
		background-color: getColor(wit);
	}

	&.block-crs {
		min-height: 300px;
		position: relative;

		.block-title {
			font-weight: normal;
		}
		.block-icon {
			height: 1.35rem;
			width: 1.35rem;
			margin-right: 0.5rem;
		}
		.block-list {
			.interfaceLink {
				font-size: getFontSize(small);
			}
		}
		.block-moreLink {
			border-top: 1px solid getColor(grijs light);
			position: absolute;
			//top: 0;
			bottom: 0.5rem;
			//left: 1rem;
			//right: 1rem;
			font-size: getFontSize(small);
			padding-top: 0.5em;
			width: 90%;
			@include hideUnderline;
		}
	}

	.postTitle {
		font-weight: bold;
		display: block;
	}

	.postDate {
		font-weight: normal;
		font-style: italic;
		font-size: getFontSize(extrasmall);
	}

	// Right Column
	&--side {
		margin-top: 0;
		margin-bottom: 1.5em;
		border-bottom: 4px solid getColor(grijs border);
		padding-bottom: 0.5em;
		padding-left: 0;

		&:last-child {
			border-bottom: none;
		}
	}

	// Promoblock (met afbeelding)
	&-promo {
		padding: 0;
		position: relative;
		min-height: 300px;

		.block-image {
			margin-top: -0.5em;
			position: relative;
		}
		.block-label {
			position: absolute;
			bottom: 0.75em;
			right: -0.25em;
			padding: 0.25em 0.75em;
			font-size: getFontSize(small);

			&--violet {
				background-color: getColor(violet default);
				color: getColor(wit);
			}
		}
		.block-title {
			padding: 0 $space/2;
		}
		.block-text {
			padding: 0 $space/2;
		}
		.block-link {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			//background-image: none;
			text-indent: 1000%; //snelle hack voor Chrome
			white-space: nowrap;
			overflow: hidden;
			display: block;
			//z-index: 2;
			background: getColor(wit);
			opacity: 0;
		}
		&:hover, &:active, &:focus {
			transition: all 0.2s ease-in-out;
			transform: scale(1.025);
		}
		&--violet {
			.block-label {
				background-color: getColor(violet default);
				color: getColor(wit);
			}
			&:hover, &:active, &:focus {
				.block-title,
				.block-text {
					color: getColor(violet default);
				}
			}
		}
		&--green {
			.block-label {
				background-color: getColor(mosgroen default);
				color: getColor(wit);
			}
			&:hover, &:active, &:focus {
				.block-title,
				.block-text {
					color: getColor(mosgroen default);
				}
			}
		}
	}

	&--blue {
		background-color: getColor(hemelblauw lightest);

		.crs-wrapper & {
			background-color: getColor(wit);
		}
	}
	&--grey {
		background-color: getColor(grijs lightest);

		.crs-wrapper & {
			background-color: getColor(wit);
		}
	}
	&--border {
		border: 1px solid getColor(hemelblauw default);

		.crs-wrapper & {
			background-color: getColor(wit);
		}
	}

	&--purple {
		.block-title {
			color: getColor(paars default);
		}
		.block-icon {
			fill: getColor(paars default);
		}
		.interfaceLink {
			color: getColor(grijs darkest);
			fill: getColor(paars default);

			&:hover,
			&:active,
			&:focus {
				color: getColor(paars default);
				fill: getColor(paars default);
			}
		}

		.crs-wrapper & {
			background-color: getColor(wit);
		}
	}
	&--green {
		.block-title {
			color: getColor(mosgroen default);
		}
		.block-icon {
			fill: getColor(mosgroen default);
		}
		.interfaceLink {
			color: getColor(grijs darkest);
			fill: getColor(mosgroen default);

			&:hover,
			&:active,
			&:focus {
				color: getColor(mosgroen default);
				fill: getColor(mosgroen default);
			}
		}

		.crs-wrapper & {
			background-color: getColor(wit);
		}
	}
	&--violet {
		.block-title {
			color: getColor(violet default);
		}
		.block-icon {
			fill: getColor(violet default);
		}
		.interfaceLink {
			color: getColor(grijs darkest);
			fill: getColor(violet default);

			&:hover,
			&:active,
			&:focus {
				color: getColor(violet default);
				fill: getColor(violet default);
			}

		}

		.crs-wrapper & {
			background-color: getColor(wit);
		}
	}


}

.crs-wrapper {
	background-color: getColor(grijs lightest);
}