
// Default angular styling
[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak,.ng-hide:not(.ng-hide-animate) {
    display: none !important;
}

[visual-cloak], [data-visual-cloak], [x-visual-cloak], .visual-cloak, .x-visual-cloak {
	visibility: hidden;
}

// For hiding manually
.is-hidden:not(.ng-animate) {
    display: none !important;
}

// For specificity so ng-hide can be overridden
.is-forceShowing.is-forceShowing {
	display: block !important;
}