/* LISTS */

ul, ol {
	margin-top: 0;
	margin-bottom: 0;
	ul, ol {
		margin-bottom: 0;
		ul, ol {
			margin-bottom: 0;
		}
	}
}

li {
	margin: 0;
	padding: {
		top: 0.3em;
		bottom: 0.3em;
	}

}

// Unordered list
ul {
	@include bullets;
	margin-left: 1.1rem;
}

// Ordered list
ol {
	@include numbers;
	margin-left: 1.6rem;
}

// Definition List
dl {
	overflow: hidden;
}

dt {
	font-weight: bold;
}

dd {
	margin-left: 0;
	margin-bottom: 0.3em;
}

// Toptaken
.labelList {
	@include noBullets;
	margin: 1rem 0 1.5rem 0;

	&-item {
		background-color: getColor(mintgroen default);
		display: inline-block;
		padding: 0.5rem 1rem;
		margin: 0 0.5rem 0.5rem 0;
	}
	&-link {
		line-height: 1.6;
		@include noUnderline;
		color: getColor(hemelblauw darkest);
		display: inline-flex;
	    align-items: flex-start;
	    font-style: italic;
	}
	&-text {
		flex: 1 1 0;
		@include hideUnderline;
		line-height: 1.3;

		span {
			@include noUnderline;
			@include hoverEtc {
				@include underline;
				color: getColor(hemelblauw darkest);
			}
		}
	}
}